//Components
import UsuarioHeader from '../components/UserPage/UsuarioHeader';
import UsuarioInfo from '../components/UserPage/UsuarioInfo';
import UsuarioNewPublication from '../components/UserPage/UsuarioNewPublication';

//Estilos CSS
import "../css/User.css";

//Extra components
import Footer from "../components/Footer";

//Custom hook for logic
import { useUsuario } from '../hooks/useUsuario';
import Publicacion from '../components/NewsPage/Publicacion';

export const UserPage = () => {

    //Custom hook para el manejo del componente
    const { 
        userInfo,
        auth,
        isEditing,
        setIsEditing, 
        values, 
        handleInputChange, 
        handleEditInfoUser,
        setFilesList ,
        publicacionesUsuario
    } = useUsuario();

    if(userInfo === null) return <h1>Cargando informacion usuario...</h1> 
    else return (
        <>
            <div className="contenedorPrincipalUsuario">
               <UsuarioHeader 
                    userInfo={userInfo} 
                    isEditing={isEditing} 
                    setIsEditing={setIsEditing} 
                    values={values} 
                    handleInputChange={handleInputChange} 
                    handleEditInfoUser={handleEditInfoUser} 
                    setFilesList={setFilesList}
                    auth={auth}
                />
                <section className="contenedorSecundarioUsuario row">
                    <div className="col-12 col-xl-4">
                        <UsuarioInfo 
                            userInfo={userInfo} 
                            isEditing={isEditing} 
                            values={values} 
                            handleInputChange={handleInputChange}
                        />
                    </div>
                    <div className="col-12 col-xl-8 mt-3 mt-lg-0">
                        {
                            userInfo.uid === auth.uid && <UsuarioNewPublication userInfo={userInfo}/> 
                        }
                        <div className="publicacionesUser">
                            {
                                publicacionesUsuario.map(publicacion => <Publicacion publicacion={publicacion} key={publicacion._id}/>)
                            }
                        </div>
                   </div>
                </section>
            </div>
            <Footer/>
        </>
    )
}
